<template>
  <div class="category">
    <!-- 顶部搜索框 -->
    <van-search class="seach" shape="round" disabled placeholder="请输入搜索关键词" />

    <!-- IndexBar 索引栏 -->
    <van-index-bar :index-list="indexList" class="sortBox" :sticky="false">
      <!-- 分类列表 -->
      <ul class="sortList">
        <li v-for="(item,i) in sortList" :key="i">
          <!-- 锚点位置 -->
          <van-index-anchor class="Partition" :index="item.cate_name">
            <!-- 分割线 -->
            <van-divider :style="{ color: '#000', borderColor: '#000', padding: '0 16px' }">{{item.cate_name}}</van-divider>
          </van-index-anchor>

          <!-- 商品列表 -->
          <ol class="chlid">
            <li v-for="(val,k) in item.children" :key="k" @click="onclick(val.id)">
              <img :src="val.pic">
              <p class="title">{{val.cate_name}}</p>
            </li>
          </ol>
        </li>
      </ul>
    </van-index-bar>
  </div>
</template>
<script>
import "../assets/style/Category.less"
export default {
  name: "meut",
  data() {
    return {
      indexList: [
        "家电电器",
        "服饰鞋帽",
        "影音设备",
        "智能设备",
        "出行交通",
        "餐厨厨房",
        "手机数码",
        "热门推荐",
        "居家生活",
      ],
      sortList:[]
    };
  },
  beforeRouteEnter(to,from, next){
    next(vm=>{
      vm.axios(vm.baseUrl+"/category").then(data=>{
        vm.sortList = data.data.data;
      })
    })
  },
  methods:{
    onclick(id){
      console.log(id);
      this.$router.push({name:'GoodsList',params:{val:id}})
    }
  }
};
</script>
<style lang="less" scoped>

</style>






